import React from "react";
import "../../App.css";

function Footer() {
  const footerPos = {
    bottom: "0"
  };
  return (
    <React.Fragment>
      {/* <!-- Footer --> */}
      <footer
        className="page-footer font-small footer-body text-light pt-4 footerPos"
        style={footerPos}
      >
        {/* <!-- Copyright --> */}
        <div className="footer-copyright text-center py-3">
          Copyright ©2019 | Rank Finder
        </div>
        {/* <!-- Copyright --> */}
      </footer>
      {/* <!-- Footer --> */}
    </React.Fragment>
  );
}

export default Footer;
