import React, { Component } from "react";
import HearderDefault from "../layouts/header-default";
import Footer from "../layouts/footer";
import { Link } from "react-router-dom";
import "../../css/simple-line-icons.css";
import imgLoader from "../../images/Double Ring-1s-200px.gif";
// import Home from "./home";

/*
- Create JSON Object for all the taxis and it's destinations
- If a user "retreat" it should show all the taxis that travel to and from retreat taxi rank
- Initially, all taxi ranks will be diplayed and then narrowed down in the search  

*/

class Results extends Component {
  //Solves the warning :Warning: Can't perform a React state update on an unmounted component. This is a no-op, but it indicates a memory leak in your application. To fix, cancel all subscriptions and asynchronous tasks in the componentWillUnmount method.
  _isUnMounted = false;

  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      resultData: [],
      //Getting the data from the URL
      term: this.props.match.params.searchText,
      //Stores the return from the pushToMaps() function
      waypoints: []
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this.fetchData();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  //Fetching routes api
  fetchData() {
    fetch(
      "https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-routes/master/rank-finder-routes.json"
    )
      .then(response => response.json())
      .then(parsedJSON => {
        // console.log(parsedJSON);
        this.setState({
          //When data is retrieved isLoaded changes to true and data from JSON Object is pushed into resultData array
          isLoaded: true,
          resultData: parsedJSON.routes
        });
      });
  }
  //Getting the id and sending the coordinates to maps JS
  pushToMaps(id) {
    //Returning the Object based on the id of the row in the table
    //So if the id of the row is equal to the id in the database (resData.id) then return the whole Object in which that id is in
    let result = this.state.resultData.find(resData => resData.id === id);
    console.log(id, result.waypoints);
    // this.routeChange();
    //Changing the state of waypoints based on the id
    this.setState({
      waypoints: result.waypoints
    });
  }
  routeChange = () => {
    let path = `/maps`;
    this.props.history.push(path);
  };
  render() {
    const { term, resultData, error, isLoaded, waypoints } = this.state;

    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return (
        <div className="container loader align-items-center">
          <div className="row h-100 align-items-center">
            <img src={imgLoader} alt="loader" />
          </div>
        </div>
      );
    } else {
      return (
        <React.Fragment>
          <HearderDefault />
          {/* Header introduction to page */}
          <div className="jumbotron jumbotron-fluid mb-0 ">
            <div className="container">
              <h1 className="display-4">Results</h1>
              <h3 className="lead">Here's what we managed to find.</h3>
            </div>
          </div>
          <section className="custom-result-bg">
            <div className="container py-4">
              <div className="table-responsive">
                <table className="table table-hover table-borderless">
                  <caption>Results of Routes</caption>
                  <thead className="table-heading">
                    <tr>
                      <th scope="col">From</th>
                      <th scope="col">To</th>
                      <th scope="col">Checkpoints</th>
                      <th scope="col">Price</th>
                      <th scope="col" />
                    </tr>
                  </thead>

                  {resultData
                    .filter(
                      //Looking for any characters in JSON Object (API) matching user input
                      //Using "new RegExp()" object for case sensitivity. i = ignoreCase and g = global (anywhere in the string)
                      routes =>
                        new RegExp(term, "i", "g").test(routes.from) ||
                        new RegExp(term, "i", "g").test(routes.to) ||
                        new RegExp(term, "i", "g").test(routes.checkpoints)
                    )
                    .map(route => {
                      //If there is a result then return those results
                      if (
                        route.from !== "" ||
                        route.to !== "" ||
                        route.checkpoints !== ""
                      ) {
                        return (
                          <tbody key={route.id}>
                            <tr>
                              <td>{route.from}</td>
                              <td>{route.to}</td>
                              <td>{route.checkpoints.toUpperCase()}</td>
                              <td>{route.price}</td>
                              <td>
                                <Link
                                  //Getting the id of each item
                                  //Using onFocus event to called function. onClick does not work well.
                                  onFocus={() => this.pushToMaps(route.id)}
                                  to={{
                                    pathname: "/maps",
                                    //Using state to send props (data) to maps component
                                    state: {
                                      term: term,
                                      waypoints: waypoints
                                    }
                                  }}
                                  className="btn button custom-button-all"
                                >
                                  See on Map
                                </Link>
                              </td>
                            </tr>
                          </tbody>
                        );
                      } else {
                        return <p>"No results found!"</p>;
                      }
                    })}
                </table>
              </div>
              <div className="container text-center">
                <div className="btn-group btn-group-toggle">
                  <Link className="btn btn custom-button-all" to="/">
                    Try Again
                  </Link>
                </div>
              </div>
            </div>
          </section>

          <Footer />
        </React.Fragment>
      );
    }
  }
}

export default Results;
