import React from "react";
import imgLoader from "../../images/Double Ring-1s-200px.gif";

const Posts = ({ posts, loading }) => {
  if (loading) {
    return (
      <div className="container post-loader align-items-center">
        <div className="row h-100 text-center align-items-center">
          <img src={imgLoader} className="mx-auto" alt="loader" />
        </div>
      </div>
    );
  }

  return (
    <div className="row">
      {posts.map(post => (
        <div key={post.title} className="col-lg-4 col-md-6 col-sm-12">
          <div className="card m-2">
            <div className="card-body">
              <h5 className="card-title">{post.title} </h5>

              <p className="card-text">{post.description}</p>
            </div>
            <div className="card-footer card-footer-top">
              <small className="text-muted">{post.publishedAt}</small>
            </div>
            <a
              className="card-footer-link"
              rel="noopener noreferrer "
              href={post.url}
              target="_blank"
            >
              <div className="card-footer card-footer-bottom">
                <small className=""> Full Article</small>
              </div>
            </a>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Posts;
