import React, { Component } from "react";
import $ from "jquery";
import { Link } from "react-router-dom";
import logo from "../../images/Rank-Finder-Logo.png";
import "../../css/creative-home.css";

class HeaderHome extends Component {
  activeItem = () => {
    // Add background colour to navbar on scroll
    $(window).scroll(function() {
      $("nav").toggleClass("scrolled", $(this).scrollTop() > 50);
    });
  };

  render() {
    return (
      <React.Fragment>
        {this.activeItem()}
        {/* <!-- Navigation --> */}
        <nav
          className="navbar navbar-expand-lg navbar-light bg-dar fixed-top py-3"
          id="mainNav"
        >
          <div className="container">
            <Link to="/" className="navbar-brand js-scroll-trigger">
              <img src={logo} alt="Rank Finder Logo" />
            </Link>
            <button
              className="navbar-toggler navbar-toggler-right"
              type="button"
              data-toggle="collapse"
              data-target="#navbarResponsive"
              aria-controls="navbarResponsive"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse" id="navbarResponsive">
              <ul className="navbar-nav ml-auto my-2 my-lg-0">
                <li className="nav-item">
                  <Link to="/" className="nav-link js-scroll-trigger">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/newsfeed" className="nav-link js-scroll-trigger">
                    News
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/tips" className="nav-link js-scroll-trigger">
                    Tips
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/about" className="nav-link js-scroll-trigger">
                    About
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/contact" className="nav-link js-scroll-trigger">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </React.Fragment>
      /*              <Link className="nav-item nav-link" to="/">
                Home
              </Link>
              <Link className="nav-item nav-link" to="/newsfeed">
                News
              </Link>
              <Link className="nav-item nav-link" >
                Tips
              </Link>
              <Link className="nav-item nav-link" to="/about">
                About
              </Link>
              <Link className="nav-item nav-link" to="/contact">
                Contact
              </Link>*/
    );
  }
}

export default HeaderHome;
