import React from "react";
import { Link } from "react-router-dom";

class BackButton extends React.Component {
  render() {
    // console.log(this.props.term);
    return (
      <React.Fragment>
        <footer className="page-footer font-small  text-light pt-4">
          <div className="container-fluid">
            <div className="container-fluid text-center text-md-left">
              <div className="row">
                <Link
                  // Appends the user's query to the URL of the results page. The results page takes the user's query from the URL and does another search
                  //
                  to={`/results/${this.props.term}`}
                  className="btn button custom-button-all maps-back-button"
                  style={{
                    zIndex: "50"
                  }}
                >
                  Back
                </Link>
              </div>
            </div>
          </div>
        </footer>
      </React.Fragment>
    );
  }
}
export default BackButton;
