import React from "react";
import "../../App.css";
import HearderDefault from "../layouts/header-default";
import Footer from "../layouts/footer";

function About() {
  return (
    <React.Fragment>
      <HearderDefault />
      {/* Header introduction to page */}
      <div className="jumbotron jumbotron-fluid mb-0 ">
        <div className="container">
          <h1 className="display-4">About</h1>
          <h3 className="lead">A little bit about us.</h3>
        </div>
      </div>

      <div className="container-fluid bg-image custom-about">
        <div className="row no-gutter">
          <div className="col-lg-2" />

          <div className="col-lg-10">
            <section className="custom-about-bg">
              <h2 className="pb-3">Why do we exist?</h2>
              <p>
                Rank Finder serves to address the social issue with regards to
                the lack of information in the taxi industry.
              </p>
              <p>
                Taxis are the most relied upon mode of public transport in South
                Africa and especially in Cape Town. The problem lies with the
                lack of information within the taxi industry and this website
                serves as a digital platform to centralise information so that
                it may be accessible to the public.
              </p>
            </section>
          </div>
          <div className="col-lg-2" />
        </div>
      </div>
      <Footer />
    </React.Fragment>
  );
}

export default About;
