import React from "react";
import HearderDefault from "../layouts/header-default";
import Footer from "../layouts/footer";

function Contact() {
  return (
    <React.Fragment>
      <HearderDefault />
      <div className="container-fluid h-100 ">
        <div className="row h-100 ">
          <div className="col-md-4 col-lg-6 p-5 custom-contact">
            <ul class="list-group text-light">
              <li class="list-group-item bg-transparent">
                <i className="icon-call-in pr-3" /> Phone : 0840840840{" "}
              </li>
              <li class="list-group-item bg-transparent">
                <i className="icon-envelope pr-3" /> Email :
                rankfindercpt@gmail.com
              </li>
            </ul>
          </div>
          <div className="col-md-8 col-lg-6">
            <div className="container mb-5 mt-5 .custom-form">
              <h3 className="login-heading text-center">Contact Us</h3>
              <form
                method="GET"
                action="mailto:rankfindercpt@gmail.com"
                // enctype="text/plain"
              >
                <div className="form-group mt-3  ">
                  <div className="input-group mb-2">
                    <div className="input-group-prepend ">
                      <div className="input-group-text">
                        <i className="icon icon-user text-info" />
                      </div>
                    </div>
                    <input
                      type="text"
                      className="form-control"
                      id="username"
                      name="username"
                      placeholder="Enter name here..."
                      required
                    />
                  </div>
                </div>
                <div className="form-group">
                  <div className="input-group mb-2">
                    <div className="input-group-prepend">
                      <div className="input-group-text">
                        <i className="icon icon-envelope text-info" />
                      </div>
                    </div>
                    <input
                      type="email"
                      className="form-control"
                      id="email"
                      name="email"
                      placeholder="Enter email here..."
                      required
                    />
                  </div>
                </div>

                <div className="form-group">
                  <div className="input-group mb-2">
                    <div className="input-group-prepend">
                      <div className="input-group-text">
                        <i className="icon icon-pencil text-info" />
                      </div>
                    </div>
                    <textarea
                      className="form-control"
                      placeholder="Type message here..."
                      required
                    />
                  </div>
                </div>

                <div className="text-center">
                  <button
                    type="submit"
                    value="Send"
                    className="btn custom-button-all"
                  >
                    Send
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </React.Fragment>
  );
}

export default Contact;
