import React from "react";
//Import below fixes the following notice: mapbox-gl.js:305 This page appears to be missing CSS declarations for Mapbox GL JS, which may cause the map to display incorrectly.
import "mapbox-gl/dist/mapbox-gl.css";
import DeckGL from "@deck.gl/react";
import { PathLayer, IconLayer } from "@deck.gl/layers";
import ReactMapGL, { Marker } from "react-map-gl"; //Adding BaseMap
import ICON_IMAGE from "../../images/rankfinder-map-marker.png";
import BackButton from "../layouts/backBotton";

const MAPBOX_ACCESS_TOKEN =
  "pk.eyJ1IjoicmFua2ZpbmRlciIsImEiOiJjand1dnhtbzEwMWhlNDRvOG15ZWNoZDNoIn0.ex4SGCrn0unDhWm8rhpn6g";

class Maps extends React.Component {
  state = {
    data: [{ coordinates: [18.37755, -33.94395] }],
    //Taking in the props from results component. Located in the <Link/> prop where state:{} is used as a prop to send data
    waypoints: this.props.location.state.waypoints,
    endPointLat: this.props.location.state.waypoints[
      this.props.location.state.waypoints.length - 1
    ][1],
    endPointLng: this.props.location.state.waypoints[
      this.props.location.state.waypoints.length - 1
    ][0],
    //Getting user query so that it can be sent to BackButton component
    term: this.props.location.state.term
  };
  _onHover(info) {
    if (info.object) {
      // eslint-disable-next-line
      return (
        <div
          style={{
            position: "absolute",
            padding: "4px",
            background: "rgba(0, 0, 0, 0.8)",
            color: "#fff",
            maxWidth: "300px",
            height: "400px",
            fontSize: "10px",
            zIndex: 10,
            pointerEvents: "none",
            top: info.y,
            left: info.x
          }}
        >
          {alert(info.coordinate + " x: " + info.x + " y: " + info.y)}
        </div>
      );
    }
  }

  render() {
    const { waypoints, endPointLng, endPointLat, term } = this.state;

    const layers = [
      new PathLayer({
        data: [
          {
            path: waypoints,
            name: "Richmond - Millbrae",
            color: [255, 0, 0]
          }
        ],
        id: "path-layer",
        pickable: true,
        widthScale: 20,
        widthMinPixels: 5,
        getPath: d => d.path,
        getColor: [28, 62, 89, 120],
        getWidth: 0.01
      }),
      new IconLayer({
        id: "icon-layer",
        data: [{ longitude: endPointLng, latitude: endPointLat }],
        iconAtlas: ICON_IMAGE,
        iconMapping: {
          marker: { x: 0, y: 0, width: 50, height: 50, anchorY: 0, mask: true }
        },
        getIcon: d => "marker",
        sizeScale: 8,
        getPosition: d => [d.longitude, d.latitude],
        getSize: 6,
        pickable: true
      })
    ];
    //Getting props from results page
    console.log(this.props.location.state.term, this.state.endPointLng);
    return (
      <React.Fragment>
        <DeckGL
          initialViewState={{
            longitude: this.state.waypoints[0][0],
            latitude: this.state.waypoints[0][1],
            zoom: 15,
            pitch: 0,
            bearing: 0
          }}
          controller={true}
          layers={[layers]}
        >
          <ReactMapGL
            mapStyle="mapbox://styles/mapbox/streets-v9"
            mapboxApiAccessToken={MAPBOX_ACCESS_TOKEN}
          >
            <Marker
              latitude={this.state.waypoints[0][1]}
              longitude={this.state.waypoints[0][0]}
              offsetTop={-12.5}
              offsetLeft={-12.5}
            >
              <div>
                <img
                  style={{
                    width: "50%"
                  }}
                  src={ICON_IMAGE}
                  alt="map marker"
                />
              </div>
            </Marker>
          </ReactMapGL>
        </DeckGL>
        {/* Sending user query to BackButton component */}
        <BackButton term={term} />
      </React.Fragment>
    );
  }
}

export default Maps;
