import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../../App.css";
import "../../css/simple-line-icons.css";
// import ReactDisqusComments from "react-disqus-comments";
import logoBanner from "../../images/Find-Your-Rank---Logo-True-Final.png";
import taxiStop from "../../images/rank-finder-taxi-bus-stop-vector.png";
import newsVector from "../../images/rank-finder-news-vector.png";
import tipsVector from "../../images/rank-finder-tips-info-board-vector.png";
import HeaderHome from "../layouts/header-home";
import Footer from "../layouts/footer";
import { Scene, Controller } from "react-scrollmagic";
import { Tween, Timeline } from "react-gsap";
//import ScrollMagic from "scrollmagic";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: "",
      valText: ""
    };
    this.searchHandler = this.searchHandler.bind(this);
    this.routeChange = this.routeChange.bind(this);
  }
  //Disqus Comment Handler
  handleNewComment = comment => {
    console.log(comment.text);
  };
  routeChange = () => {
    let path = `/results/${this.state.searchText}`;
    this.props.history.push(path);
  };

  searchHandler = event => {
    // console.log(event.target.value);
    this.setState({ searchText: event.target.value });
  };

  //For UX - Allows users to press enter to search result
  handleKeyPress = event => {
    if (event.key === "Enter") {
      this.routeChange();
    }
  };

  componentDidMount() {
    // Set a variable for our button element.
    const scrollToTopButton = document.getElementById("js-top");

    // Let's set up a function that shows our scroll-to-top button if we scroll beyond the height of the initial window.
    const scrollFunc = () => {
      // Get the current scroll value
      let y = window.scrollY;

      // If the scroll value is greater than the window height, let's add a class to the scroll-to-top button to show it!
      if (y > 720) {
        scrollToTopButton.className = "top-link show";
      } else {
        scrollToTopButton.className = "top-link hide";
      }
    };

    window.addEventListener("scroll", scrollFunc);

    const scrollToTop = () => {
      // Let's set a variable for the number of pixels we are from the top of the document.
      const c = document.documentElement.scrollTop || document.body.scrollTop;

      // If that number is greater than 0, we'll scroll back to 0, or the top of the document.
      // We'll also animate that scroll with requestAnimationFrame:
      // https://developer.mozilla.org/en-US/docs/Web/API/window/requestAnimationFrame
      if (c > 0) {
        window.requestAnimationFrame(scrollToTop);
        // ScrollTo takes an x and a y coordinate.
        // Increase the '10' value to get a smoother/slower scroll!
        window.scrollTo(0, c - c / 10);
      }
    };

    // When the button is clicked, run our ScrolltoTop function above!
    scrollToTopButton.onclick = function(e) {
      e.preventDefault();
      scrollToTop();
    };

    /**
     *  RECOMMENDED CONFIGURATION VARIABLES: EDIT AND UNCOMMENT THE SECTION BELOW TO INSERT DYNAMIC VALUES FROM YOUR PLATFORM OR CMS.
     *  LEARN WHY DEFINING THESE VARIABLES IS IMPORTANT: https://disqus.com/admin/universalcode/#configuration-variables
     */

    // var disqus_config = function () {
    //     this.page.url = PAGE_URL;  // Replace PAGE_URL with your page's canonical URL variable
    //     this.page.identifier = PAGE_IDENTIFIER; // Replace PAGE_IDENTIFIER with your page's unique identifier variable
    // };

    (function() {
      // DON'T EDIT BELOW THIS LINE
      var d = document,
        s = d.createElement("script");

      s.src = "https://randfinder.disqus.com/embed.js";

      s.setAttribute("data-timestamp", +new Date());
      (d.head || d.body).appendChild(s);
    })();
  }

  // Validation for the search button. User cannot submit a search without entering a field.
  validate = event => {
    if (this.state.searchText === "") {
      let append = document.getElementById("info-container");
      // append.classList.add("border-danger");
      append.innerHTML =
        '<div class="alert alert-danger alert-dismissible fade show" role="alert"> <strong>Required! </strong> Please add text into the input field.<button type="button" class="close" data-dismiss="alert" aria-label="Close">        <span aria-hidden="true">&times;</span>      </button>    </div>';
      event.preventDefault();
      return false;
    }
  };
  render() {
    return (
      <React.Fragment>
        {/* <!-- Masthead --> */}
        <HeaderHome />

        <header className="masthead">
          <div className="container h-100">
            <div className="row h-100 align-items-center">
              <div className="col-lg-6">
                <Tween
                  duration={1}
                  from={{
                    opacity: 0,
                    x: -20
                  }}
                >
                  <h1 className="text-uppercase">FIND YOUR TAXI</h1>

                  <p className="intro-paragraph">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    Donec nec ullamcorper sem, id blandit dolor. Etiam porta
                    euismod rhoncus.
                  </p>

                  <form className="form-row pt-2">
                    <div className="input-group">
                      <input
                        type="text"
                        className="form-control custom-input"
                        placeholder="Enter text here"
                        aria-label="Add a destination"
                        aria-describedby="basic-addon2"
                        value={this.state.searchText}
                        onKeyPress={this.handleKeyPress}
                        onChange={this.searchHandler}
                        required
                      />

                      <div className="input-group-append ml-3 trigger-news-image">
                        {/* Appending userinput to results URL */}

                        <Link
                          to={`/results/${this.state.searchText}`}
                          type="submit"
                          onClick={this.validate}
                        >
                          <button className="btn custom-button-all">
                            Search
                          </button>
                        </Link>
                      </div>
                    </div>
                  </form>
                </Tween>
              </div>
              <div className="col-lg-6 lg text-center">
                <div className="header-image-container">
                  <div className="image-1">
                    <Tween
                      duration={1}
                      from={{
                        opacity: 0,
                        y: 20
                      }}
                    >
                      <img src={taxiStop} alt="Rank finder logo icon" />
                    </Tween>
                  </div>
                  <div className="image-2 ">
                    <Tween
                      duration={1}
                      from={{
                        opacity: 0,
                        y: -20
                      }}
                    >
                      <img src={logoBanner} alt="Rank finder logo icon" />
                    </Tween>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* Popup for validation of form input */}
          <div id="info-container" />
          <div className="" />
        </header>

        {/* Back to top button */}
        <a className="top-link hide bg-primary" href="/" id="js-top">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 6">
            <path d="M12 6H0l6-6z" />
          </svg>
          <span className="screen-reader-text">Back to top</span>
        </a>

        {/* News Section */}
        <section className="custom-section trigger-news">
          <div className="container h-100">
            <div className="row h-100 align-items-center">
              <div className="d-none d-lg-block col-lg-6 col-md-12 ">
                <Controller>
                  <Scene triggerElement=".trigger-news">
                    <Timeline>
                      <Tween
                        from={{
                          opacity: 0,
                          x: -200
                        }}
                        to={{
                          x: 0,
                          opacity: 1
                        }}
                      >
                        <h3 className="news-heading">News</h3>
                      </Tween>
                    </Timeline>
                  </Scene>
                </Controller>
                <div className="indent-box">
                  <Controller>
                    <Scene triggerElement=".trigger-news">
                      <Timeline>
                        <Tween
                          from={{
                            opacity: 0,
                            x: -200
                          }}
                          to={{
                            x: 0,
                            opacity: 1
                          }}
                        >
                          <p className="news-heading">
                            Get all the latest news in taxi industry from taxi
                            strikes to traffic alerts, incidents to price
                            changes. All from reliable sources such as News24,
                            Indepent Online and TimesLIVE and more. Click on the
                            button below to see all the articles.
                          </p>
                        </Tween>
                      </Timeline>
                    </Scene>
                  </Controller>
                  <Controller>
                    <Scene triggerElement=".trigger-news">
                      <Timeline>
                        <Tween
                          from={{
                            opacity: 0,
                            x: -200
                          }}
                          to={{
                            opacity: 1,
                            x: 0
                          }}
                        >
                          <div>
                            <Link
                              to="/newsfeed"
                              className="custom-button-all news-heading"
                            >
                              All News
                            </Link>
                          </div>
                        </Tween>
                      </Timeline>
                    </Scene>
                  </Controller>
                </div>
              </div>
              {/*Mobile and Table Screen only (No animtion)*/}
              <div className="d-lg-none d-xl-none col-lg-6 col col-md-12">
                <h3 className="news-heading">News</h3>

                <div className="indent-box">
                  <p className="news-heading">
                    Get all the latest news in taxi industry from taxi strikes
                    to traffic alerts, incidents to price changes. All from
                    reliable sources such as News24, Indepent Online and
                    TimesLIVE and more. Click on the button below to see all the
                    articles.
                  </p>

                  <div>
                    <Link
                      to="/newsfeed"
                      className="custom-button-all news-heading"
                    >
                      All News
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-12">
                <Controller>
                  <Scene
                    triggerElement=".trigger-news"
                    triggerHook="onLeave"
                    offset={-200}
                    duration="35%"
                  >
                    {/* Call Timeline Progress Percentage */}
                    {progress => (
                      <Tween
                        duration={1}
                        from={{
                          x: 100,
                          opacity: 0
                        }}
                        to={{
                          x: 0,
                          opacity: 1
                        }}
                        delay={-0.7}
                        totalProgress={progress}
                        paused
                      >
                        <img src={newsVector} alt="rank finder news vector" />
                      </Tween>
                    )}
                  </Scene>
                </Controller>
              </div>
            </div>
          </div>
        </section>

        {/* Tips section */}
        <section className="custom-section trigger-tips">
          <div className="container h-100">
            <div className="row h-100 align-items-center">
              <div className="col-lg-6 col-md-12 tips-image-show-lg">
                <Controller>
                  <Scene
                    triggerElement=".trigger-tips"
                    triggerHook="onLeave"
                    offset={-150}
                    duration="50%"
                  >
                    {/* Call Timeline Progress Percentage */}
                    {progress => (
                      <Tween
                        duration={1}
                        from={{
                          x: -100,
                          opacity: 0
                        }}
                        to={{
                          x: 0,
                          opacity: 1
                        }}
                        delay={-0.7}
                        totalProgress={progress}
                        paused
                      >
                        <img
                          src={tipsVector}
                          alt="rank finder tips info board vector"
                        />
                      </Tween>
                    )}
                  </Scene>
                </Controller>
              </div>
              <div className="d-none d-lg-block col-lg-6 col-md-12">
                <Controller>
                  <Scene triggerElement=".trigger-tips">
                    <Timeline>
                      <Tween
                        from={{
                          opacity: 0,
                          x: 200
                        }}
                        to={{
                          x: 0,
                          opacity: 1
                        }}
                      >
                        <h3 className="news-heading">Tips</h3>
                      </Tween>
                    </Timeline>
                  </Scene>
                </Controller>
                <div className="indent-box ">
                  <Controller>
                    <Scene triggerElement=".trigger-tips">
                      <Timeline>
                        <Tween
                          from={{
                            opacity: 0,
                            x: 200
                          }}
                          to={{
                            x: 0,
                            opacity: 1
                          }}
                        >
                          <p>
                            New to communiting via taxi? There are a few
                            unspoken rules if you choose to take a minibus. Here
                            are few safety and 'etiquette' tips to make sure are
                            prepared. Go ahead and click on the button below to
                            see all the tips.
                          </p>
                        </Tween>
                      </Timeline>
                    </Scene>
                  </Controller>
                  <Controller>
                    <Scene triggerElement=".trigger-tips">
                      <Timeline>
                        <Tween
                          from={{
                            opacity: 0,
                            x: 200
                          }}
                          to={{
                            opacity: 1,
                            x: 0
                          }}
                        >
                          <div>
                            <Link to="/tips" className="custom-button-all">
                              See All
                            </Link>
                          </div>
                        </Tween>
                      </Timeline>
                    </Scene>
                  </Controller>
                </div>
              </div>
              {/*Mobile and Table Screen only (No animtion)*/}
              <div className="d-lg-none d-xl-none col-lg-6 col-md-12">
                <h3>Tips</h3>
                <div className="indent-box">
                  <p>
                    New to communiting via taxi? There are a few unspoken rules
                    if you choose to take a minibus. Here are few safety and
                    'etiquette' tips to make sure are prepared. Go ahead and
                    click on the button below to see all the tips.
                  </p>

                  <Link to="/tips" className="custom-button-all">
                    See All
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Disqus */}

        <section className="custom-section trigger-disqus">
          <div className="container h-100">
            <div className="row h-100 align-items-center ">
              <div className="col-lg-12 pt-5 text-center">
                <Controller>
                  <Scene triggerElement=".trigger-disqus" offset={-100}>
                    <Timeline>
                      <Tween
                        from={{
                          opacity: 0,
                          y: -200
                        }}
                        to={{
                          y: 0,
                          opacity: 1
                        }}
                      >
                        <h3 className="news-heading">Join the Discussion</h3>
                      </Tween>
                    </Timeline>
                  </Scene>
                </Controller>
                <Controller>
                  <Scene triggerElement=".trigger-disqus" offset={-100}>
                    <Timeline>
                      <Tween
                        from={{
                          opacity: 0,
                          y: -200
                        }}
                        to={{
                          y: 0,
                          opacity: 1
                        }}
                      >
                        <p className="mx-auto">
                          If you have any news on taxi in your area please feel
                          to share it here.
                        </p>
                      </Tween>
                    </Timeline>
                  </Scene>
                </Controller>
              </div>

              <div className="col-lg-12 m-0">
                <div id="disqus_thread" />
              </div>
            </div>
          </div>
        </section>

        <Footer />
      </React.Fragment>
    );
  }
}

export default Home;
