import React, { useState, useEffect } from "react";
import HearderDefault from "../layouts/header-default";
import Footer from "../layouts/footer";
import axios from "axios";
import Posts from "../layouts/Posts";
import Pagination from "../layouts/Pagination";

const NewsFeed = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(9);

  //useEffect runs when the component mounts or updates
  useEffect(() => {
    const fetchPosts = async () => {
      setLoading(true);
      const res = await axios.get(
        "https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-newsfeed/master/rank-finder-newsfeed.json"
      );
      setPosts(res.data.articles);
      setLoading(false);
    };

    fetchPosts();
  }, []);

  //"https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-newsfeed/master/rank-finder-newsfeed.json"

  //Get current posts
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);

  //Change Page
  const paginate = (e, pageNumber) => {
    e.preventDefault();
    setCurrentPage(pageNumber);
  };
  return (
    <React.Fragment>
      <HearderDefault />
      <div className="jumbotron jumbotron-fluid mb-0">
        <div className="container">
          <h1 className="display-4">News</h1>
          <h3 className="lead">
            The latest news from the taxi industry right here
          </h3>
        </div>
      </div>
      <section className="body-background">
        <div className="container custom-news">
          <Posts posts={currentPosts} loading={loading} />
          <Pagination
            postsPerPage={postsPerPage}
            totalPosts={posts.length}
            paginate={paginate}
          />
        </div>
      </section>
      <Footer />
    </React.Fragment>
  );
};

export default NewsFeed;
